body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.Layout{
    margin: 10px;
}
.result {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.result div {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.result .number {
  color: black;
  background: white;
  border-radius: 50%;
  width: 24px;
  text-align: center;
  margin: 0 5px;
}
.empty {
  background: #4b4149;
}
.filled {
  background: #de474b;
}
ul {
  list-style-type: none;
  margin: 10px 0;
  padding: 0;
}
.cart {
  background: white;
  padding: 5px 15px;
  margin: 10px;
  border-radius: 20px;
}
.cart a {
  font-size: 80%;
  color: black;
  text-decoration: none;
}

.Goods__good {
  display: flex;
  border-bottom: #4b4149 solid 1px;
  padding: 10px 0;
}
.Goods__good .idField {
  width: 10%;
}
.Goods__good .name {
  width: 30%;
}
.Goods__good .rating {
  width: 10%;
  color: gray;
}
.Goods__good .trand {
  width: 10%;
  color: #00a900;
}
.Goods__good .priceSegment {
  width: 20%;
}
.Goods__good .priceSegment .segment {
  border: #bfbfbf solid 1px;
  border-radius: 15px;
  padding: 0 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.Goods__good .comments {
  width: 10%;
  color: orange;
}
.Goods__good .comments svg {
  color: #bababa;
}
.Goods__good .checked {
  width: 10%;
  padding: 0 3%;
}
.Goods__good .checked svg {
  color: #bababa;
  cursor: pointer;
}
.Goods__good .checked .fa-check-square {
  color: #de474b;
}

.Goods__Header {
  display: flex;
  border-bottom: #4b4149 solid 1px;
  padding: 10px 0;
  color: #685d66;
}
.Goods__Header .idField {
  width: 10%;
}
.Goods__Header .name {
  width: 30%;
}
.Goods__Header .rating {
  width: 10%;
}
.Goods__Header .trand {
  width: 10%;
}
.Goods__Header .priceSegment {
  width: 20%;
}
.Goods__Header .comments {
  width: 10%;
}
.Goods__Header .checked {
  width: 10%;
}

.shopping_result {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.shopping_result div {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0 3px;
}
.advantages {
  width: 100%;
  display: flex;
  margin: 30px 0;
  justify-content: space-between;
}
.advantages .advantage {
  display: flex;
  width: 30%;
  align-items: center;
}
.advantages .advantage .advantageNum {
  border: orange solid 1px;
  font-size: 300%;
  color: orange;
  padding: 0 20px;
}
.advantages .advantage .advantageText {
  margin: 10px;
}
.separator {
  height: 50px;
  background: url(/static/media/разделение.bc95661d.png) center;
  background-size: cover;
}
.forU {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 20px auto 40px ;
}

